import URL from "./config";

function Fetch(url, options) {
  this.setState({
    loading: true
  });
  return new Promise(resolve => {
    fetch(
      URL.dev + "/rest/action/upload/customer/data_of_hospital" + url,
      options
    )
      .then(response => {
        return response.json();
      })
      .then(responseJson => {
        this.setState({
          loading: false
        });
        resolve(responseJson);
      })
      .catch(error => {
        console.error(error);
      });
  });
}

export default Fetch;
