import React from "react";
import { Wrapper, NavWrapper, SelectFile, ImportResult, Title } from "./styled";
import { withRouter } from "react-router";
import qs from "querystring";

class ImportFile extends React.PureComponent {
  state = {
    page: "",
    token: ""
  };
  componentDidMount() {
    // 防止页面刷新时 导入成功的结果显示不正确
    const _currentPage = this.props.location.pathname;
    if (_currentPage !== "/form") {
      this.setState({
        page: "/form"
      });
    }
    const token_str = this.props.location.search.replace(/\?/, "");
    if (token_str) {
      const { token } = qs.parse(token_str);
      this.setState({
        token: token
      });
    }
  }
  componentWillReceiveProps() {
    const _currentPage = this.props.location.pathname;
    this.setState({
      page: _currentPage
    });
  }
  render() {
    const { children } = this.props;
    const { page, token } = this.state;
    return (
      <Wrapper>
        <Title>数据导入</Title>
        <NavWrapper>
          <SelectFile>选择文件</SelectFile>
          <ImportResult page={page}>导入结果</ImportResult>
        </NavWrapper>
        {children({ token })}
      </Wrapper>
    );
  }
}

export default withRouter(ImportFile);
