import React from "react";
import {
  Wrapper,
  Tip,
  BackBtn,
  TipDetail,
  Table,
  TableTitle,
  Icon
} from "./styled";
import _ from "lodash";

class Result extends React.Component {
  state = {
    type: "",
    data: ""
  };
  componentWillMount() {
    let _type = this.props.match.params.id;
    if (_type !== "success") {
      const errorMsg = localStorage.getItem("ExportMsg");
      let _data = JSON.parse(errorMsg);
      this.setState({
        data: _data
      });
    }
    this.setState({
      type: _type
    });
  }
  renderTr = () => {
    let { data } = this.state;
    if (_.isArray(data)) {
      return data.map((item, index) => {
        return (
          <tr key={index} className="table-content">
            <td>{item}</td>
          </tr>
        );
      });
    } else {
      return (
        <tr className="table-content">
          <td>{data}</td>
        </tr>
      );
    }
  };
  renderTable = () => {
    return (
      <Table>
        <tbody>
          <tr className="table-head">
            <td>错误信息</td>
          </tr>
          {this.renderTr()}
        </tbody>
      </Table>
    );
  };
  failLayout() {
    let { data } = this.state;
    return (
      <>
        {/* {!(data instanceof Object) ? (
          <TipDetail>{JSON.stringify(this.state.data)}</TipDetail>
        ) : (
          this.renderTable()
        )} */}
        {/* <TableTitle>发现错误数据</TableTitle> */}
        {data && this.renderTable()}
      </>
    );
  }
  render() {
    let { type } = this.state;
    return (
      <Wrapper>
        <span>
          <Icon
            className={
              type === "success"
                ? "iconfont icon-tianjiachenggong"
                : "iconfont icon-gantanhao"
            }
          />
        </span>
        <Tip>{type === "success" ? "导入成功" : "导入出现问题"}</Tip>
        {type !== "success" && this.failLayout()}
        <BackBtn tag="button" to="/form">
          返回
        </BackBtn>
      </Wrapper>
    );
  }
}

export default Result;
