import React from "react";
import { Alert, Upload } from "antd";
import "antd/dist/antd.min.css";
import styled from "styled-components";
import {
  FormWrap,
  SelectBtn,
  FileName,
  TagA,
  SelectFile,
  AlertBox,
  ProgressTip
} from "./styled";
import alertTip from "../../utils/tip_box";
import Fetch from "../../utils/fetch";
import { Spin, Icon } from "antd";
import { withRouter } from "react-router";

const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
let formData = "";

class ExportDataForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isAllow: false,
      file_name: "",
      is_show: false,
      message: "",
      duration: 4,
      loading: false
    };
  }
  // 选择上传文件
  changeFile = e => {
    let arr = e.target.value.split("\\");
    const file_type_arr = arr[arr.length - 1].split(".");
    const file_type = file_type_arr[file_type_arr.length - 1];
    if (file_type !== "xlsx") {
      alertTip.bind(this, "请选择后缀为.xlsx的excel文件", 4000)();
      return false;
    }
    this.setState({
      file_name: arr[arr.length - 1]
    });
  };
  deleteFile = e => {
    this.setState({
      file_name: ""
    });
  };
  // 是否禁止选择文件的按钮
  isAllow = e => {
    // 清空之前选中的文件
    e.target.value = "";
    if (!this.state.isAllow || this.state.file_name) e.preventDefault();
  };
  flag = true;
  submitFile = e => {
    e.preventDefault();
    if (!this.state.file_name) {
      if (this.flag === false) return false;
      this.flag = false;
      alertTip.bind(this, "请选择导入文件", 4000)();
      return false;
    }
    // 发送请求给后端 后端返回信息
    const _token = this.props.token || "";
    const _form = e.target;
    formData = new FormData(_form);
    var name = formData.get("customerType");
    console.log(_form, formData, name);
    Fetch.bind(this, `?token=${_token}`, {
      method: "POST",
      headers: {},
      body: formData
    })().then(res => {
      const code = res.head.code;
      if (code === 200) {
        this.props.history.push("/form/result/success");
      } else {
        let errorMsg = res.body.errorMsg;
        localStorage.removeItem("ExportMsg");
        localStorage.setItem(
          "ExportMsg",
          errorMsg ? JSON.stringify(errorMsg) : '["' + res.head.msg + '"]'
        );
        this.props.history.push("/form/result/fail");
      }
    });
  };
  // input select
  changeSelect = e => {
    if (e.target.value !== "--请选择--") {
      this.setState({
        isAllow: true
      });
    } else {
      this.setState({
        isAllow: false
      });
    }
  };
  render() {
    const { file_name, isAllow, is_show, message, loading } = this.state;
    return (
      <>
        {loading ? (
          <ProgressTip loading={loading}>
            <Spin indicator={antIcon} />
          </ProgressTip>
        ) : (
          <>
            <AlertBox is_show={is_show ? 1 : 0} className="row">
              <Alert
                className="ant-col-lg-6 ant-col-lg-offset-2 ant-col-sm-6 ant-col-sm-offset-10"
                message={message}
                type="error"
              />
            </AlertBox>
            <FormWrap>
              <form action="" method="post" onSubmit={this.submitFile}>
                <div>
                  <div className="controls">
                    <label htmlFor="select" className="select-label">
                      <em style={{ color: "red" }}>*</em>导入数据类型
                    </label>
                    <select
                      name="customerType"
                      className="select-type"
                      onChange={this.changeSelect}
                    >
                      <option defaultValue="--请选择--">--请选择--</option>
                      <option value="user_info">用户</option>
                      <option value="hcp">客户</option>
                      <option value="clm_presentation">媒体材料</option>
                    </select>
                    <TagA
                      file_name={1}
                      href="https://fcstatic.crmpower.cn/dev/wxmini/template.zip"
                    >
                      模板下载
                    </TagA>
                  </div>
                  <div className="controls">
                    <label htmlFor="file" className="file-label">
                      <em style={{ color: "red" }}>*</em>选择导入文件
                    </label>
                    <FileName>
                      {file_name}
                      <TagA
                        onClick={this.deleteFile}
                        file_name={file_name ? 1 : 0}
                        href="javascript:;"
                      >
                        删除
                      </TagA>
                    </FileName>
                    <SelectBtn
                      file_name={file_name ? 1 : 0}
                      isAllow={isAllow ? 1 : 0}
                      type="btn"
                    >
                      浏览{isAllow}
                    </SelectBtn>
                    <SelectFile
                      name="file"
                      onClick={this.isAllow}
                      file_name={file_name ? 1 : 0}
                      onChange={this.changeFile}
                      type="file"
                    />
                  </div>
                  <div className="controls">
                    <button type="submit" className="import-file">
                      导入
                    </button>
                  </div>
                </div>
              </form>
            </FormWrap>
          </>
        )}
      </>
    );
  }
}

export default withRouter(ExportDataForm);
